import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'dc-ui-header-flyout-nav-link',
  styleUrl: 'dc-ui-header-flyout-nav-link.scss',
  shadow: true,
})
export class DcUiHeaderFlyoutNavLink {
  @Prop() label: string;
  @Prop() href: string;
  @Prop() disabled?: boolean;

  componentWillLoad() {
    if (!this.href) {
      this.disabled = true;
    }
  }

  render() {
    return (
      <a
        href={this.href}
        class={this.disabled ? 'disabled' : ''}>
        <span>{this.label || <slot />}</span>
      </a>
    );
  }
}
